<template>
  <div id="DetailInfo">

    <div class="detailInfo-main-part">

      <div class="detailInfo-steps-div">
        <el-steps :space="200" :active="step">
          <el-step title=" 1" icon="edit"></el-step>
          <el-step title=" 2" icon="information"></el-step>
          <el-step title=" 3" icon="picture"></el-step>
        </el-steps>
      </div>

    <transition name="fade">
      <div class="detaiInfo-form" v-if="step===2">
        <el-form ref="form" :model="form" label-width="250px">

          <el-form-item label="">
            <el-radio-group v-model="form.gender">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
          </el-form-item>

          <el-form-item label="">
              <el-autocomplete
             class="inline-input"
             v-model="form.school"
             :fetch-suggestions="querySearch"
             placeholder=""
             :trigger-on-focus="false"
             @select="handleSelect"
           ></el-autocomplete>
          </el-form-item>

          <el-form-item label=",">
            <el-date-picker
             v-model="form.birthday"
             type="date"
             placeholder=""
             :picker-options="pickerOptions0">
           </el-date-picker>
          </el-form-item>

           <el-form-item label="">
               <el-input v-model="form.name"></el-input>
           </el-form-item>

           <el-form-item label="">
              <el-input v-model="form.address"></el-input>
           </el-form-item>

           <el-form-item>
             <el-button type="primary" @click="_onSubmit"></el-button>
             <el-button type="warning"></el-button>
           </el-form-item>

        </el-form>
      </div>
      </transition>

    <transition name="fade">
      <div class="headpic-uploader">

        <div>

        </div>

      </div>
    </transition>

    </div>



  </div>
</template>

<script>
import schools from './schools.js'
import AliyunOssUpload from '../../component/AliyunOssUpload.vue'


export default {

  name: "DetailInfo",

  data(){
    return {
    //
    hostname: '47.94.129.112',
    //
    step: 3,

    //
    form: {
      name: '',
      address: '',
      birthday: '',
      gender: '',
      school: '',
    },
    schools: '',


  }
},

  methods: {
    onSubmit() {
      console.log('submit!');
    },
    pickerOptions0: {
      disabledDate(time) {
        return time.getTime() < Date.now() - 8.64e7;
      }
    },
    querySearch(queryString, cb) {
      var schools = this.schools;
      var results = queryString ? schools.filter(this.createFilter(queryString)) : schools;
      //  callback 
      cb(results);
    },
    createFilter(queryString) {
      return (school) => {
        return (school.value.indexOf(queryString.toLowerCase()) === 0);
      };
    },
    _onSubmit(){
      const t = this
      //
      //

      //
      fetch(`http://${t.hostname}/api/set_detailInfo`, {
          method: 'post',
          body: 'username=' + localStorage.username + '&name=' + t.form.name + '&address=' + t.form.address + '&birthday=' + t.form.birthday + '&gender=' + t.form.gender + '&school=' + t.form.school ,
          headers: {
            "Accept": "application/json",
            "Content-Type": "application/x-www-form-urlencoded"
          },
        })
        .then(re => re.json())
        .then(re => {
          if(re.msg == "success"){
            //
            this.step = 3;
          }
        })

    },
  },


    mounted() {
      this.schools=schools
    },

}
</script>


<style lang="scss" scoped>
@import "./DetailInfo.scss"

</style>



